// React
import React from "react";

// Internal dependencies
import "./main.css";


export default class Main extends React.Component
{
    /* LIFECYCLE */

    constructor(props)
    {
        super(props);

        this.state = { data: null };
    }

    componentDidMount()
    {
        window.fetch(`https://api.ipify.org/?format=json`)
            .then(response => response.json())
            .then(json => {
                window.fetch(`https://ipapi.co/${json.ip}/json/`)
                    .then(response => response.json())
                    .then(json => this.setState({ data: `${json.city}, ${json.country_name}` }));
            });
    }


    /* VISUALS */

    render()
    {
        return(
            <main>
                <p>You are from {this.state.data}</p>
                {this.props.children}
            </main>
        );
    }
}